footer{
    background:var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a{
    color: var(--color-bg);
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 1rem;
    display: inline-block;
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:2rem;
    margin: 0 auto 3rem;
}

.footer__socials{
     display: flex;
     justify-content: center;
     gap: 1rem;
     margin-bottom: 4rem;
}

.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyrights{
    margin-bottom: 4rem;
    background: var(--color-bg);
    color: var(--color-primary);
    padding: 0.8rem;
    border-radius: 0.5rem;;
}

footer p{
    width: 50%;
    justify-content: center;
    padding-bottom: 2rem;
    margin-left: 25%;
    color: var(--color-bg);
}

/* MEDIA QUIERIES (medium device) */

@media screen and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/* MEDIA QUIERIES (small device) */

@media screen and (max-width:600px) {
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }
    
    .footer__socials{
        margin-bottom: 2.6rem;
    }

    footer{
        margin-top: 70%;
    }
}

/* MEDIA QUIERIES (small device) */

@media screen and (max-width:600px)
{
    footer{
        margin-top: 80%;
    }
}